/** generic
 **/
*,
*:before,
*:after {
  box-sizing: border-box; }

/** base
 **/
body {
  margin: 0; }

table {
  width: 100%;
  border-spacing: 0;
  border-collapse: inherit; }

/** objects
 **/
.o-box {
  background-color: white;
  border-radius: .25rem;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.08);
  padding: 1.5rem; }

.o-center {
  align-items: center;
  display: flex;
  justify-content: center; }

/** components
 **/
.c-header {
  height: 80px;
  background-color: #1565c0;
  position: relative;
  z-index: 995; }

/** utility
 **/
.u-mb-0 {
  margin-bottom: 0; }

.u-mb-8 {
  margin-bottom: 0.5rem; }

.u-mb-16 {
  margin-bottom: 1rem; }

.u-mb-24 {
  margin-bottom: 1.5rem; }

.u-mb-32 {
  margin-bottom: 2rem; }

.u-mb-40 {
  margin-bottom: 2.5rem; }

.u-mb-48 {
  margin-bottom: 3rem; }

.u-mb-56 {
  margin-bottom: 3.5rem; }

.u-mb-64 {
  margin-bottom: 4rem; }

/** trumps
 **/
.__hover:hover, .__hover:focus {
  color: #1e88e5; }
